body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.orangeBg {
    background-color: #E78E4F;
}

.whiteBg {
    background-color: #E7DFDC;
}

.blackBg {
    background-color: #0C1A1D;
    color: #E7DFDC;
}

.greyBg {
    background-color: #455147;
}

.pinkBg {
    background-color: #EFB293;
}

.App {
    text-align: center;
    color: #E7DFDC;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 10vh;
    width: 7vw;
}

.App-header {
    min-height: 10vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

footer {
    min-height: 10vh;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.Main {
    display: flex;
    flex-flow: column;
    min-height: 80vh;
}
.Results{
    display: flex;
    width: 100vw;
    justify-content: center;
    flex-wrap: wrap;
    margin-bottom: 10px;
}
.SearchResults {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 850px;
    margin: 50px 0;
}
.Thumbnail {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 150px;
    margin: 10px;
}

.Thumbnail:hover img {
    border: 1px solid cornflowerblue;
    -webkit-transition: .3s;
    transition: .3s;
}

h1 {
    font-size: 1em;
}

img {
    display: flex;
    margin: 0;
    height: 200px;
    width: 100%;
    border: 1px solid black;
    border-radius: 5px;
}

p,
h1,
a {
    color: darkgray;
    text-align: left;
    margin: 0;
    text-decoration: none;
}
.PrevLink {
    align-self: left;
    position: relative;
}
.InfoLayout {
    display: flex;
    flex-wrap: wrap;
    padding: 5vh 0;
    min-width: 250px;
    max-width: 1200px;
    padding: 10vh 1vw;
    margin: 30px 0;
}

.MoviePage, .SeriesPage, .EpisodePage {
    display: flex;
    flex-flow: column;
    align-items: center;
}

.column {
    display: flex;
    flex-flow: column;
    padding: 0 10px;
}

.row {
    display: flex;
    flex-flow: row;
    width: 100%;
}

.row p {
    padding: 0;
    text-align: left;
}

.row h1 {
    display: flex;
    margin-bottom: 10px;
    font-size: 2em;
}

.row h3 {
    text-align: left;
    padding: 0;
    margin-right: 10px;
    font-size: 0.7em;
}

.row img {
    min-height: 550px;
    width: 350px;
    margin: 0 auto;
}

.bottom {
    flex-grow: 1;
    align-items: flex-end;
}

.seasonLinks {
    justify-content: center;
}

@media only screen and (max-width: 768px) {
    /* For mobile phones: */
    [class*="mobile"] {
        width: 100%;
        flex-flow: column;
    }
}
.Input {
    display: flex;
    box-sizing: border-box;
    border: 1px solid #420062;
    height: 5vh;
    width: 30vw;
    font-size: 1.5em;
    padding: 10px;
    margin: 30px auto;
}
.Button {
    background: #282c34;
    display: flex;
    box-sizing: border-box;
    border: 1px solid whitesmoke;
    align-items: center;
    justify-content: center;
    width: 20vw;
    font-size: 24px;
    color: white;
    height: 5vh;
    margin: 30px auto;
}

.Button:hover {
    background: #d7ddd3;
    color: #282c34;
    cursor: pointer;
}

.Button:active {
    background: #200020;
    color: white;
}

.Movies,
.Series {
    display: flex;
    margin: 0 auto;
    box-sizing: border-box;
    border: 1px solid black;
    border-radius: 5px;
    justify-content: center;
    align-items: center;
    width: 10vw;
    height: 5vh;
    color: black;
}

.Movies:hover,
.Series:hover {
    background-color: #59C3C3;
    color: white;
}

.Series {
    background-color: black;
    color: white;
}
.SearchBar{
    display: flex;
    margin: 10px;
}
